export default class Game {

  constructor(canvas) {
    this.canvas = canvas
    this.context = canvas.getContext("2d")
    this.width = canvas.width
    this.height = canvas.height
    this.hw = this.canvas.width / 2
    this.hh = this.canvas.height / 2
    this.wBulged = this.canvas.width * 0.90

    this.lines = new Image()
    this.lines.src = "data:image/png;base64,"

    this._setupfakeCRT()


    this.keys = {
      32: 'space',
      37: 'left',
      38: 'up',
      39: 'right',
      40: 'down',
      87: 'w',
      65: 'a',
      83: 's',
      68: 'd'
    }

    this.keyPressed = {}

    this._addListenerMulti(document, 'keydown keyup', (event) => {
      const keyName = this.keys[event.keyCode]

      if (keyName) {
        this.keyPressed[keyName] = event.type === 'keydown';
        event.preventDefault();
      }
    })

  }

  update() {
    this.entities.forEach((entity) => {
      if (entity.update) entity.update()
    })
  }

  draw() {
    this.context.drawImage(this.lines, 0, 0, this.width, this.height)
    this.texture.loadContentsOf(this.canvas)

    if (this.glcanvas) {
      this.glcanvas.draw(this.texture)
        .bulgePinch(this.hw, this.hh, this.wBulged, 0.12)
        .vignette(0.25, 0.74)
        .update()
    }

    this.entities.forEach(  (entity) => {
      if (entity.draw) entity.draw(this.context)
    })
  }

  step() {
    let fps = 60,
      interval = 1000 / fps,
      updated = false

    while (this.timeSinceLastUpdate < new Date().getTime()) {
      this.update()
      updated = true
      this.timeSinceLastUpdate += interval
    }

    if (updated) this.draw()
    updated = false
  }

  onFrame(callback) {
    if (window.requestAnimationFrame) {
      requestAnimationFrame( () => {
        callback()
        this.onFrame(callback)
      })
    } else {
      let fps = 60
      setInterval(callback, 1000 / fps)
    }
  }

  start() {
    this.timeSinceLastUpdate = new Date().getTime()
    this.context.drawImage(this.lines, 0, 0, this.width, this.height)
    this.texture.loadContentsOf(this.canvas)
    this.onFrame(() => {
      this.step()
    })
  }

  _addListenerMulti(el, s, fn) {
    s.split(" ").forEach(e => el.addEventListener(e, fn, false));
  }

  _setupfakeCRT() {
    try {
      this.glcanvas = fx.canvas();
      this.texture = this.glcanvas.texture(this.canvas)
      this.canvas.parentNode.insertBefore(this.glcanvas, this.canvas)
      this.canvas.style.display = 'none'
      this.glcanvas.className = this.canvas.className
      this.glcanvas.id = this.canvas.id
      this.canvas.id = `old_${this.canvas.id}`
    } catch (e) {
      console.log("No FX")
      return
    }
  }

}



